    addToCart(result) {
      this.$nuxeo
        .operation("javascript.as_add_to_cart")
        .input(result.uid)
        .params({
          cartId: this.$store.state.cartId,
        })
        .execute()
        .then(
          function() {
            this.$toasted.success(
              this.$t("message.search-added-to-cart"),
              constants.TOASTED_DEFAULT_OPTIONS
            );
            this.$store.dispatch("updateCart");
          }.bind(this)
        )
        .catch(function(error) {
          this.$toasted.error(
            this.$t("message.search-unable-to-add"),
            constants.TOASTED_DEFAULT_OPTIONS
          );
          throw error;
        });
    },

    removeFromCart(result) {
      this.$nuxeo
        .operation("javascript.as_remove_from_cart")
        .input(result.uid)
        .params({
          cartId: this.$store.state.cartId,
        })
        .execute()
        .then(
          function() {
            this.$toasted.success(
              this.$t("message.search-removed-from-cart"),
              constants.TOASTED_DEFAULT_OPTIONS
            );
            this.$store.dispatch("updateCart");
          }.bind(this)
        )
        .catch(function(error) {
          this.$toasted.error(
            this.$t("message.search-unable-to-remove"),
            constants.TOASTED_DEFAULT_OPTIONS
          );
          throw error;
        });
    },

    select(result) {
      if (this.selects.indexOf(result) < 0) {
        this.selects.push(result);
      }
    },

    unselect(result) {
      var index = this.selects.indexOf(result);
      if (index >= 0) {
        this.selects.splice(index, 1);
      }
    },

    isSelect(result) {
      return this.selects.indexOf(result) >= 0;
    },

    addSelectionToCart() {
      this.$nuxeo
        .operation("Collection.AddToCollection")
        .input(this.selects)
        .param(
          "collection",
          "/default-domain/UserWorkspaces/Guest/Shopping_Cart"
        )
        .execute()
        .then(
          function() {
            this.$store.dispatch("updateCart");
            this.clearSelects();
            this.$refs.selectionModal.hide();
            this.$toasted.success(
              this.$t("message.Selection Added to Cart"),
              constants.TOASTED_DEFAULT_OPTIONS
            );
          }.bind(this)
        )
        .catch(function(error) {
          this.$toasted.error(
            this.$t("message.Could Not Add Selection to Cart"),
            constants.TOASTED_DEFAULT_OPTIONS
          );
          throw error;
        });
    },

    clearSelects() {
      this.selects.splice(0, this.selects.length);
    },

    isInCart(result) {
      var isInCart = this.$store.getters.isInCart(result.uid);
      return isInCart;
    },

    hasFacet(facetName) {
      return (
        this.facets[facetName] &&
        this.facets[facetName].buckets &&
        this.facets[facetName].buckets.length > 0
      );
    },

    //UI stuff

    calculateSearchFiltersPosition() {
      var $searchFilters = this.$el.querySelector(".search-filters");
      this.searchFilters.offsetTop = $searchFilters.getBoundingClientRect().top;
    },

    handleScroll() {
      this.scrollTop =
        window.pageYOffset !== undefined
          ? window.pageYOffset
          : (
              document.documentElement ||
              document.body.parentNode ||
              document.body
            ).scrollTop;

      if (this.scrollTop > this.searchFilters.offsetTop) {
        var appFooterOffsetTop = document
          .querySelector(".app-footer")
          .getBoundingClientRect().top;
        var searchFiltersHeight = this.$el.querySelector(".search-filters")
          .clientHeight;

        if (appFooterOffsetTop < searchFiltersHeight) {
          this.searchFilters.isStickyBottom = true;
          this.searchFilters.isSticky = false;
        } else {
          this.searchFilters.isSticky = true;
          this.searchFilters.isStickyBottom = false;
        }
      } else {
        this.searchFilters.isSticky = false;
        this.searchFilters.isStickyBottom = false;
      }
    },

    toggleFiltersOnMobile() {
      this.searchFilters.isVisibleOnMobile = !this.searchFilters
        .isVisibleOnMobile;
    },
  },

  created() {
    if (this.$route.query.brandId) {
      this.queryParams.product_search_brand_agg = JSON.stringify([
        this.$route.query.brandId,
      ]);
    }

    window.addEventListener("scroll", this.handleScroll);
  },

  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  }
};
</script>

